import React from 'react';
import axios from "axios";

class NavbarLoggedIn extends React.Component {



    constructor(props) {
        super(props);
        this.state = {


            userID: null,


            user: '',

            authenticated: null,


        };

    }

    componentDidMount() {


        axios
            .get('/api/users/userCheck', {withCredentials: true}).then(response => {
            if (typeof response.data.user !== 'undefined')
                this.setState({
                    user: response.data.user,
                    userID: response.data.user.user_id,
                    authenticated: true
                });
            else {
                this.setState({
                    user: {},
                    authenticated: false
                });
            }

            console.log(this.state.authenticated)

            console.log(this.state.user.first_name)

        }).catch(error => {
            console.log(error)
            return this.setState({
                user: {},
                authenticated: false
            });
        });


    }


        render() {
        return (
            <nav className="navbar navbar-expand-lg navbar-light navbar-desktop nav-active">
                <a className="navbar-brand" href="/">
                    BOOKCAMP
                </a>
                <button
                    className="navbar-toggler"
                    type="button"
                    data-toggle="collapse"
                    data-target="#navbarText"
                    aria-controls="navbarText"
                    aria-expanded="false"
                    aria-label="Toggle navigation"
                >
                    <span className="navbar-toggler-icon" />
                </button>
                <div className="collapse navbar-collapse" id="navbarText">
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item">
                            <a className="nav-link" href={'/'}>
                                Всі табори
                            </a>
                        </li>
                        <li className="nav-item">
                            <a
                                className="nav-link margin-right--25-desktop"
                                href={`/user-profile`}
                            >
                                <button className="enter-button-navbar">
                                    {this.state.user.first_name} {this.state.user.last_name}
                                </button>
                            </a>
                        </li>
                        {/*<li className="nav-item">*/}
                        {/*    <a*/}
                        {/*        className="nav-link margin-no-navbar"*/}
                        {/*        href={`/registrate-user`}*/}
                        {/*    >*/}
                        {/*        <button className="registrate-button-navbar">*/}
                        {/*            Реєстрація*/}
                        {/*        </button>*/}
                        {/*    </a>*/}
                        {/*</li>*/}
                    </ul>
                </div>
            </nav>
        );
    }
}

export default NavbarLoggedIn;